<!-- 确定兑换 -->
<template>
	<view class="confirm-exchange xh-dialog" :style="{display: isShow?'block':'none'}">
		<view class="xh-dialog-body animated bounceInUp">
			     <!-- toast 背景 -->
				 <image class="dialog-bg" src="../../../static/images/dialog_bg.png"></image>
		         <!-- toast header --> 
				 <image class="dialog-bg-header-26" src="../../../static/images/dialog_tips_header.png"></image>
				 <!-- 主要内容 -->
				<view class="confirm-exchange-body">
					   <!-- title -->
					   <view class="ce-title">核对兑换数量</view>
					   <!-- msg -->
					   <view class="ce-msg">您当前是否兑换<text class="num">{{checkData.length}}</text>罐？</view>
					   <!-- 备注 -->
					   <view class="remarks">备注：点击按钮扫<view class="stress">商家店铺码</view>兑换</view>
				</view>
				<view class="xh-dialog-btn animated rubberBand delay-1s">
					  <view class="xdb-item">
						<button v-if="userInfo.mobile" class="xdb-item exchange" @click="exchange">马上兑换</button>
						<button v-else class="xdb-item exchange" open-type="getPhoneNumber" @getphonenumber="exchangeBefore">马上兑换</button>
						<image class="xdb-item-bg" src="../../../static/images/dialog_btn_bg01.png"></image>
					  </view>
				</view>
		</view>
		<!-- 关闭按钮 -->
		<image class="xh-dialog-close fadeInOpacity" :style="{display: isShow?'block':'none'}" src="../../../static/images/toast_close.png" @click="close"/>
		<!-- 黑幕 -->
		<view class="xh-dialog-black"  @touchmove.stop></view>
	</view>
	
</template>

<script>
	import {
		mapMutations,
	    mapActions,
		mapGetters} from 'vuex'
		import {setCheckCardVolume} from '@/utils/auth.js'
		import log from '@/utils/log.js'
		export default{
			data(){
				return {
					checkData:[],
					isShow:false
				}
			},
			computed: {
				...mapGetters(['userInfo']),
			},
			methods:{
				...mapActions({
					updateUserMobile: 'login/updateUserMobile',
					getUserInfo: 'login/getUserInfo'
				}),
				...mapMutations({
					setCheckCardVolume:'personal/setCheckCardVolume'
				}),
				show(data){
					this.checkData = data
					this.isShow = true
				},
				close(){
					this.isShow = false
				},
				//兑换前
				exchangeBefore(e) {
				      
					this.isLoading = true
						
					this.updateUserMobile(e).then(() => {
						//开始兑换
						this.exchange()
						//异步更新用户信息
						this.getUserInfo(true)
						//重置状态
						this.isLoading = false
						
					}).catch(()=>{
						
						this.isLoading = false
						
					})
					  
				},
				exchange(){
					//数据深copy
					let checkData = JSON.parse(JSON.stringify(this.checkData))
					log.setFilterMsg('卡包兑换数据')
					log.info("卡包兑换数据=>" + JSON.stringify(checkData))
					//本地缓存一份
					setCheckCardVolume(checkData)
					this.setCheckCardVolume(checkData)
					this.$router.navigateTo({url:'/pages/personal/storesCode/index'})
					this.isShow = false
				}
			}
		}
	</script>
	
	
</script>

<style lang="scss">
	.confirm-exchange-body {
			position: absolute;
			top: 350rpx;
			left: 50%;
			transform: translateX(-50%);
			width: 450rpx;
			display: flex;
			flex-direction: column;
			align-items: center;

		.ce-title {
			font-size: RPX(20);
			color: #fff;
			text-align: center;
			font-weight: bold;
		}
		
		.change-now{
			margin-bottom: 0;
			margin-top: 0;
		}

		.ce-msg {
			font-size: RPX(14);
			color: #F6E2B3;
			margin-top: 30rpx 0;
		}
		
		.num{
			font-size:RPX(25);
			color: #fff;
		}

		.remarks {
			font-size: RPX(15);
			color: #FFFFFF;
			text-align: center;
			margin-top: RPX(11);
			margin-bottom: RPX(19);
			display: flex;
			align-items: center;
		}
		.stress{
			color: #F6E2B3;
		}
	}
	.confirm-exchange{
		.exchange{
			color: #614A00;
		}
		.xh-dialog-close{
				top: 960rpx;
		}
		.xh-dialog-body{
			top: 180rpx; 
		}
			
	} 
</style>
